.bpm-cmpt-inputselect-wrapper{border:1px solid #dddddd;position:relative;background-color:white;box-sizing:border-box;padding-right:32px;padding-left:2px;min-height:30px;border-radius:2px;transition:all 0.3s}.bpm-cmpt-inputselect-wrapper:hover{border-color:#4285f4}.bpm-cmpt-inputselect-value{position:relative;display:flex;align-items:flex-start;justify-content:flex-start;flex-flow:row wrap}.bpm-cmpt-inputselect-value-item{display:flex;align-items:center;text-overflow:ellipsis;white-space:nowrap;color:#333333;font-size:12px;padding:2px 8px;border-radius:2px;margin:2px;border:1px solid #dddddd;background-color:#f5f5f5}.bpm-cmpt-inputselect-value-item-btn{margin-left:4px}.bpm-cmpt-inputselect-value-placeholder{position:relative;font-size:12px;color:#cccccc;padding:0 8px;line-height:30px}.bpm-cmpt-inputselect-trigger{position:absolute;right:0;top:0;width:30px;align-items:center;justify-content:center;display:flex;height:100%;cursor:pointer;font-size:14px;color:#333333;transition:all 0.3s}.bpm-cmpt-inputselect-trigger:hover{color:#4285f4}.bpm-cmpt-inputselect-dropdown{border-radius:2px;box-shadow:0 3px 6px -4px rgba(0,0,0,0.12),0 6px 16px 0 rgba(0,0,0,0.08),0 9px 28px 8px rgba(0,0,0,0.05)}.bpm-cmpt-inputselect--textarea .bpm-cmpt-inputselect-wrapper{min-height:90px}
