.m-bpm-cmpt-popup{position:fixed;left:0;top:0;bottom:0;right:0;width:100%;height:100%;z-index:1000;visibility:visible;overflow:hidden;transition:opacity 0.3s}.m-bpm-cmpt-popup-header{height:48px;border-bottom:1px solid #e7e7e7;position:relative}.m-bpm-cmpt-popup-header-l,.m-bpm-cmpt-popup-header-r,.m-bpm-cmpt-popup-header-title{position:absolute;top:50%;display:flex;align-items:center;justify-content:center}.m-bpm-cmpt-popup-header-l,.m-bpm-cmpt-popup-header-r{transform:translateY(-50%)}.m-bpm-cmpt-popup-header-l{left:12px}.m-bpm-cmpt-popup-header-r{right:12px}.m-bpm-cmpt-popup-header-title{left:50%;transform:translate(-50%, -50%);font-size:15px;color:#2a304a}.m-bpm-cmpt-popup-header-close{width:25px;height:25px;display:flex;align-items:center;justify-content:center;color:#c7c7cc}.m-bpm-cmpt-popup-mask,.m-bpm-cmpt-popup-wrapper{position:absolute}.m-bpm-cmpt-popup-mask{left:0;top:0;bottom:0;right:0;width:100%;height:100%;background:#000;opacity:0.6}.m-bpm-cmpt-popup-wrapper{bottom:0;left:0;width:100%;box-sizing:border-box;border-radius:15px 15px 0px 0px;background-color:white;transition:all 0.3s}.m-bpm-cmpt-popup.hidden{opacity:0;visibility:hidden}.m-bpm-cmpt-popup.hidden .m-bpm-cmpt-popup-wrapper{transform:translateY(100%)}.m-bpm-cmpt-popup--right .m-bpm-cmpt-popup-header{height:36px}.m-bpm-cmpt-popup--right .m-bpm-cmpt-popup-content{flex:1;overflow:auto}.m-bpm-cmpt-popup--right .m-bpm-cmpt-popup-wrapper{min-width:260px;display:flex;flex-flow:column nowrap;top:0;bottom:0;left:unset;right:0;height:100%;width:unset;border-radius:0px}.m-bpm-cmpt-popup--right.hidden .m-bpm-cmpt-popup-wrapper{transform:translateX(100%);opacity:0}
