.bpm-cmpt-address-m{padding:16px 0;min-height:400px;position:relative}.bpm-cmpt-address-b{min-height:58px;border-top:1px solid #eeeeee;padding-top:16px}.bpm-cmpt-address-list{display:flex;flex-flow:row wrap;align-items:flex-start;justify-content:flex-start}.bpm-cmpt-address-list .ant-radio-wrapper,.bpm-cmpt-address-list .ant-checkbox-wrapper{padding:4px;padding-top:0;display:flex;align-items:center}.bpm-cmpt-address-value{display:flex;align-items:center;justify-content:flex-start;overflow:auto}.bpm-cmpt-address-value-item{position:relative}.bpm-cmpt-address-value-item-btn{position:absolute;font-size:14px;right:0;top:0;opacity:0;cursor:pointer}.bpm-cmpt-address-value-item:hover .bpm-cmpt-address-value-item-btn{opacity:1}.bpm-cmpt-address-value .bpm-cmpt-address-item{width:64px;max-width:64px}.bpm-cmpt-address-empty{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%)}
